<script>
  import { onMount, onDestroy, createEventDispatcher } from 'svelte'
  import Popper from 'popper.js'
  import { preventEvent } from './utils'

  const dispatch = createEventDispatcher()

  export let list = []
  export let anchor = null
  export let open = true
  export let container = null

  let self = null
  let control = null

  $: if (!!anchor && !!self) {
    if (!!control) control.destroy()
    let config = { placement: 'bottom' }

    if (!!container)
      config.modifiers = {
        flip: {
          behavior: ['left', 'right'],
        },
        preventOverflow: {
          boundariesElement: container,
        },
      }
    control = new Popper(anchor, self, config)
  }

  $: if (open && !!control) {
    control.update()
  }

  function handleExternalClick() {
    open = false
  }

  onMount(() => {
    window.addEventListener('mousedown', handleExternalClick)
  })

  onDestroy(() => {
    window.removeEventListener('mousedown', handleExternalClick)
  })
</script>

<style>
  ul {
    list-style: none;
    padding: 8px 0;
    margin: 0 -2px 0 16px;
    background-color: #fff;
    border: 2px solid black;
    box-sizing: border-box;
    visibility: collapse;
    opacity: 0;
    transition: visibility 0.2s, opacity 0.2s;
  }

  li {
    padding: 0;
    position: relative;
    min-width: 72px;
  }

  li:hover > div {
    background-color: rgba(0, 0, 0, 0.12);
  }

  li > div {
    padding: 8px 16px;
  }

  hr {
    margin: 8px 0;
    border: none;
    border-bottom: 1px solid var(--color-black);
  }

  ul.open {
    visibility: visible;
    opacity: 1;
  }
</style>

<ul
  on:mousedown={preventEvent}
  on:touchstart{preventEvent}
  bind:this={self}
  class:open>
  {#each list as item (item.key)}
    <li
      on:touchstart={() => dispatch('click', item)}
      on:click={() => dispatch('click', item)}>
      <div>{item.label}</div>
      {#if !!item.divider}
        <hr />
      {/if}
    </li>
  {/each}
</ul>
