<script>
  import { createEventDispatcher } from 'svelte'
  import { scale } from 'svelte/transition'
  import { ripple } from 'fluido-components'
  import { preventEvent } from './utils'
  import TinyColor from '@ctrl/tinycolor'

  export let color = '#ffffff'

  $: primaryColor = new TinyColor(color).isValid ? color : '#ffffff'
  $: secondaryColor = new TinyColor(primaryColor).isDark()
    ? '#ffffff'
    : '#121212'

  export let value = ''
  export let node = null

  const dispatch = createEventDispatcher()

  function handleClick(evt) {
    dispatch('click', { target: evt.target })
    return preventEvent(evt)
  }

  function handleTouch(evt) {
    dispatch('click', { target: evt.target })
  }
</script>

<style>
  button {
    height: 40px;
    padding: 0 16px;
    border: 2px solid rgba(0, 0, 0, 0.7);
    border-radius: 22px;
    outline: none;
    transition: border 0.5s, border-radius 0.5s;
    background-color: var(--button-primary-color, #ffffff);
    color: var(--button-secondary-color, var(--color-black));
  }
</style>

<button
  bind:this={node}
  use:ripple
  on:click={handleClick}
  on:touchstart={preventEvent}
  on:touchend={handleTouch}
  trasition:scale
  style="--button-primary-color: {primaryColor}; --button-secondary-color: {secondaryColor}"
  class="type-button">
  <slot />
</button>
