<script>
  import { createEventDispatcher } from 'svelte'
  import { ripple } from 'fluido-components'
  import { scale } from 'svelte/transition'
  import { preventEvent } from './utils'

  export let node = null
  export let icon = 'add'

  const dispatch = createEventDispatcher()

  function handleClick(evt) {
    dispatch('click', { target: evt.target })
    return preventEvent(evt)
  }

  function handleTouch(evt) {
    dispatch('click', { target: evt.target })
  }
</script>

<style>
  div {
    height: 48px;
    width: 48px;
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 100%;
    grid-template-rows: 100%;
    cursor: pointer;
    border-radius: 1000000px;
    position: relative;
  }

  div:hover::before {
    content: '';
    position: absolute;
    background-color: rgba(0, 0, 0, 0.12);
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 1000000px;
  }

  i {
    grid-column: 1;
    grid-row: 1;
    line-height: 100%;
    text-align: center;
  }
</style>

<div
  use:ripple={{ center: true }}
  on:click={handleClick}
  on:touchstart={preventEvent}
  on:touchend={handleTouch}
  on:mousedown={preventEvent}
  bind:this={node}>
  {#each [icon] as ic (ic)}
    <i transition:scale class="material-icons">{ic}</i>
  {/each}
</div>
